import React, {useEffect, useState} from "react";

const ClickTestWithHooks = (props) => {
    // useState
    let [isClicked, setIsClickedCallback] = useState(true);
    // useEffect
    useEffect(() => {
        console.log(isClicked);
    }, [isClicked]); // Указываем, после изменения чего должен срабатывать useEffect.

    const textClick = () => {
        console.log('Text Double Clicked');
        // useState Callback used
        setIsClickedCallback(false);
    }
    const inputOutFocus = () => {
        console.log('input Out Focus')
        // useState Callback used
        setIsClickedCallback(true);
    }

    return (
        <>
            {isClicked &&
                <p onDoubleClick={textClick}>Click Text</p>
            }

            {!isClicked &&
                <input onMouseOut={inputOutFocus} type="text"/>
            }
        </>
    );
}

export default ClickTestWithHooks;
