import s from './Post.module.sass';
import React from "react";

const Post = (props) => {

  return (
    <div className={s.profile}>
      <div className={s.profile__img}>
        <img src="https://webstockreview.net/images/profile-icon-png-9.png" alt="" />
      </div>
      <div className={s.profile__text}>
        <h4>{props.name}</h4>
        <p>{props.message}</p>
      </div>
    </div>
  );
}

export default Post;
