
/*
* initialState
*/
let initialState = {
    users: [],
    isFetching: false,
    isDone: 'Hello it is Done!'
};

/*
* reducer
*/
const UsersReducer = (state = initialState, action) => {

    switch (action.type) {
        case 'FOLLOW':
            return {
                ...state,
                users: state.users.map(user => {
                    if (user.id === action.userId) {
                        return {...user, followed: true}
                    }
                    return user;
                })
            }
        case 'UNFOLLOW':
            return {
                ...state,
                users: state.users.map(user => {
                    if (user.id === action.userId) {
                        return {...user, followed: false}
                    }
                    return user;
                })
            }
        case 'SET_USERS':
            return {
                ...state,
                users: [...state.users, ...action.users]
            }
        case 'TOGGLE_LOADING':
            return {...state, isFetching: action.toggleLoading}
        default:
            return state;
    }
}

/*
* action creators
*/
export const follow = (userId) => ({
    type: 'FOLLOW',
    userId
})
export const unfollow = (userId) => ({
    type: 'UNFOLLOW',
    userId
})
export const setUsers = (users) => ({
    type: 'SET_USERS',
    users
})
export const toggleLoading = (toggleLoading) => ({
    type: 'TOGGLE_LOADING',
    toggleLoading
})

/*
* REDUX THUNK
*/
export const getUsersThunkCreator = () => (dispatch) => {
    // toggleLoading(true);
    fetch('http://jsonplaceholder.typicode.com/comments?postId=1')
    .then(response => response.json())
    .then(json => {
        // toggleLoading(false);
        dispatch(setUsers(json));
        }
    );
}

export default UsersReducer;