import React from "react";
import s from "./Dialogs.module.sass";
import {NavLink} from "react-router-dom";

const DialogItem = (props) => {
    return (
        <li>
            <NavLink
                to={`/dialogs/${props.id}`}
                className={s.dialog__link}
                activeClassName={s.active}>{props.name}
            </NavLink>
        </li>
    );
}

export default DialogItem;
