import React from "react";
import s from './MyPosts.module.sass';
import Post from './Post/Post';
import {Field, reduxForm} from "redux-form";
import {MaxLengthCreator, requiredFields} from "../../../utils/validators/validators";


const MyPosts = (props) => {

  // Map By Props
  let postElements = props.posts.map(post => {
    return  <Post message={post.message} name={post.name} key={post.name} />
  });


  // Redux Form Callback
  let onSubmit = (formData) => {
    // console.log(formData.message);
    props.newMessageText(formData.message);
    formData.message = '';
  }

  return (
    <>
    <div className={s.sendpost}>
      <h5>My posts</h5>
      {/*ReduxFormComponent*/}
      <ReduxLoginForm onSubmit={onSubmit}/>
    </div>
    {postElements}
    </>
  );
}

// validateCreator
let maxLength10 = MaxLengthCreator(10);
// reduxForm
const PostForm = (props) => {

  return (
      <form onSubmit={props.handleSubmit} action="">
        <div className="form-group">
          <Field
              name="message"
              component="input"
              type="text"
              validate={[requiredFields, maxLength10]}
              className="form-control" />
        </div>
        <button className="btn btn-primary">Submit</button>
      </form>
  );
}

// ReduxLoginForm
const ReduxLoginForm = reduxForm({
  form: 'PostForm'
})(PostForm);

export default MyPosts;
