import s from './Navbar.module.sass';
import {NavLink} from "react-router-dom";

function Navbar() {
  return (
    <div className="col-md-2 p-0">
      <div className={s.sidebar + ' bg-info p-15'}>
        <ul className={s.nav + " flex-column"}>
          <li className="nav-item">
            <NavLink to="/" className="nav-link">Profile</NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/dialogs" className="nav-link" activeClassName={s.active}>Messages</NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/forms" className="nav-link" activeClassName={s.active}>Forms</NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/music" className="nav-link" activeClassName={s.active}>Music</NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/users" className="nav-link" activeClassName={s.active}>Users</NavLink>
          </li>
          <li className="nav-item">
            <NavLink to="/settings" className="nav-link" activeClassName={s.active}>Settings</NavLink>
          </li>
        </ul>
      </div>
    </div>
  );
}

export default Navbar;
