import React from "react";
import s from "../Forms.module.sass";
import {Field} from "redux-form";

const SimpleForm = (props) => {
    console.log(props)
    return (
        <form onSubmit={props.handleSubmit} className={s.simpleForm} action="">
            <div className="form-group">
                <label htmlFor="input1">Email address</label>
                <Field
                    name="input1"
                    component="input"
                    type="text"
                    className="form-control" />
            </div>
            <div className="form-group">
                <label htmlFor="input12">Password</label>
                <Field
                    name="input12"
                    component="input"
                    type="text"
                    className="form-control" />
            </div>
            <div className="form-check">
                <Field
                    name="check1"
                    component="input"
                    type="checkbox"
                    className="form-check-input" />
                <label
                    className="form-check-label"
                    htmlFor="check1">Check me out
                </label>
            </div>
            <button
                type="submit"
                className="btn btn-primary">Submit
            </button>
        </form>
    );
}

export default SimpleForm;
